<script>
import restaurantService from "./services/restaurant.service";

export default {
  data() {
    return {
      get access() {
        return JSON.parse(localStorage.access);
      },
      get restaurantId() {
        return localStorage.restaurantId;
      },
      set restaurantId(newRestaurantId) {
        return (localStorage.restaurantId = newRestaurantId);
      },
      drawer: null,
      items: [
        { title: "Dashboard", icon: "mdi-view-dashboard", link: "/dashboard" },
        { title: "Orders", icon: "mdi-file", link: "/orders" },
        { title: "Daily menu", icon: "mdi-calendar", link: "/menu" },
        { title: "Dishes", icon: "mdi-book-open", link: "/dishes" },
        { title: "Customers", icon: "mdi-account", link: "/customers" },
        { title: "Billing", icon: "mdi-credit-card", link: "/billing" },
        { title: "Settings", icon: "mdi-cog", link: "/settings" },
      ],
      right: null,
      isOpen: null,
    };
  },
  // created: function() {
  //   this.$http.interceptors.response.use(undefined, function(err) {
  //     return new Promise(function() {
  //       if (err.response.status === 401 && err.config && !err.config.__isRetryRequest) {
  //         console.log(this.$route.path)
  //         this.$store.dispatch("logout");
  //         this.$router.push("/login");
  //       }
  //       throw err;
  //     });
  //   });
  // },
  mounted() {
    if (localStorage.access) {
      this.access = JSON.parse(localStorage.access);
      this.getOpenStatus();
    }
  },
  watch: {
    restaurantId(newRestaurantId) {
      localStorage.restaurantId = newRestaurantId;
    },
  },
  computed: {
    isLoggedIn: function () {
      return this.$store.getters.isLoggedIn;
    },
    disableNavigation: function () {
      if (this.$route.path === "/login" || this.$route.path === "/register")
        return false;
      return true;
    },
    isOpenColor: function () {
      if (this.isOpen === true) return "success";
      else if (this.isOpen === false) return "error";
      else return "gray";
    },
  },
  methods: {
    logout: function () {
      this.$store.dispatch("logout").then(() => {
        this.$router.push("/login");
      });
    },
    getOpenStatus: async function () {
      this.isOpen = await restaurantService.getRestaurant(
        localStorage.restaurantId,
        "isOpen"
      );
      console.log(this.isOpen);
    },
  },
};
</script>

