<template>
  <div class="overlay">
    <div class="form-wrapper">
      <v-img
        class="mb-8"
        style="margin: 0 auto"
        max-width="100px"
        src="../assets/piknik-logo-white.svg"
        alt="Piknik"
      />
      <h2 class="white--text mb-2">Dobrý deň, prosím prihláste sa.</h2>
      <p class="white--text mb-8">
        Jednoduchá administrácia reštaurácie z intuitívneho webového rozhrania.
      </p>
      <v-form ref="form" v-model="valid">
        <v-text-field
          dark
          v-model="email"
          :rules="[rules.required, rules.email]"
          label="Email"
          type="email"
          rounded
          dense
          filled
        ></v-text-field>
        <v-text-field
          dark
          v-model="password"
          :rules="[rules.required, rules.min]"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="show = !show"
          :type="show ? 'text' : 'password'"
          label="Heslo"
          rounded
          dense
          filled
        ></v-text-field>
        <v-btn
          v-on:click="login"
          dark
          color="white"
          depressed
          rounded
          :disabled="!valid || snackbar"
          >Prihlásiť sa</v-btn
        >
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <v-btn text small rounded @click="reload()" color='white'>Skúsiť znova</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  props: {
    source: String,
  },
  data: () => ({
    drawer: null,
    valid: true,
    rules: {
      required: (value) => !!value || "Povinné pole.",
      min: (v) => v.length >= 8 || "Minimálne 8 znakov.",
      email: (v) => /.+@.+\..+/.test(v) || "Email musí byť v správnom formáte.",
    },
    email: "",
    show: false,
    password: "",
    snackbar: false,
    snackbarText: 'Nesprávny email alebo heslo.',
    timeout: 0,
  }),
  methods: {
    login: function () {
      let data = {
        email: this.email,
        password: this.password,
      };
      this.$store
        .dispatch("login", data)
        .then(() => this.$router.push("/"))
        .catch((err) => {
          console.log(err);
          this.snackbar = true;
        });
    },
    reload() {
      this.email = "";
      this.password = "";
      this.snackbar = false;
    },
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #007bff;
  z-index: 10;
  display: flex;
  align-items: center;
  text-align: center;
}

.form-wrapper {
  width: 350px;
  height: auto;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .form-wrapper {
    padding: 32px;
  }
}

/deep/ .v-input__slot {
  background: rgba(255, 255, 255, 0.2) !important;
}

/deep/ .v-btn {
  color: #007bff !important;
}

/deep/ .v-application .error--text {
  color: black !important;
  caret-color: black !important;
}
</style>