<template>
  <v-skeleton-loader
    :loading="loading"
    type="article, list-item-two-line, actions"
  >
    <v-tabs centered>
      <v-tab>Account</v-tab>
      <v-tab>Restaurant</v-tab>

      <v-tab-item>
        <v-card flat>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Account
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveUserInfo()"
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="account.info.firstName"
                    label="First name"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="account.info.lastName"
                    label="Last name"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="account.info.mobile"
                    label="Phone number"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <!-- <v-col cols="12" md="6">
                  <v-select
                    :items="[{_id: '123abc', language: 'English', code: 'en'}, {_id: 'abc123', language: 'Slovenčina', code: 'sk'}]"
                    item-text="language"
                    item-value="code"
                    label="Language"
                    filled
                    rounded
                    hide-details
                  ></v-select>
                </v-col>-->
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Login credentials
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  disabled
                  text
                  rounded
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="account.email"
                    label="Email"
                    disabled
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="account.currentPassword"
                    label="Current password"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="account.newPassword"
                    label="New password"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-card>
      </v-tab-item>

      <v-tab-item>
        <v-card flat>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Online ordering
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveOnlineOrdering()"
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12">
                  <v-switch
                    class="mt-0 pt-0"
                    v-model="restaurant.isOpen"
                    :label="
                      restaurant.isOpen ? 'Enable ordering' : 'Disable ordering'
                    "
                    color="primary"
                    hide-details
                  ></v-switch>
                </v-col>
                <v-col cols="12" md="6">
                  <v-menu
                    ref="menu"
                    v-model="timePickerMenuStatus1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="time"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="restaurant.dailyMenuOrderingTime.from"
                        label="Daily menu ordering start"
                        append-icon="mdi-clock-time-four-outline"
                        readonly
                        filled
                        rounded
                        hide-details
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      v-if="timePickerMenuStatus1"
                      full-width
                      @click:minute="$refs.menu.save(time)"
                      v-model="restaurant.dailyMenuOrderingTime.from"
                      format="24hr"
                      :max="restaurant.dailyMenuOrderingTime.to"
                    ></v-time-picker>
                  </v-menu>
                </v-col>
                <v-col cols="12" md="6">
                  <v-menu
                    ref="menu"
                    v-model="timePickerMenuStatus2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="time"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="restaurant.dailyMenuOrderingTime.to"
                        label="Daily menu ordering end"
                        append-icon="mdi-clock-time-four-outline"
                        readonly
                        filled
                        rounded
                        hide-details
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      v-if="timePickerMenuStatus2"
                      full-width
                      @click:minute="$refs.menu.save(time)"
                      v-model="restaurant.dailyMenuOrderingTime.to"
                      format="24hr"
                      :min="restaurant.dailyMenuOrderingTime.from"
                    ></v-time-picker>
                  </v-menu>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Business details
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveBusinessDetails()"
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.legalName"
                    label="Legal name"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.businessId"
                    label="Business ID"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.taxId"
                    label="Tax ID"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.vat"
                    label="VAT reg. no."
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.address.street"
                    label="Street"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.address.city"
                    label="City"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.address.zip"
                    label="Zip"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.businessDetails.address.state"
                    label="State"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Names
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveNames()"
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="restaurant.names.name"
                    label="Name"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="restaurant.names.urlName"
                    label="Url name"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="restaurant.names.voiceName"
                    label="Voice name"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Information
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveRestaurantInfo()"
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="restaurant.info.contact.website"
                    label="Website"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="restaurant.info.contact.email"
                    label="Email"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="restaurant.info.contact.mobile"
                    label="Mobile"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.street"
                    label="Street"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.city"
                    label="City"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.zip"
                    label="Zip"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.state"
                    label="State"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    v-model="restaurant.info.description"
                    label="Description"
                    auto-grow
                    rounded
                    filled
                    hide-details
                  ></v-textarea>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    label="Opening hours"
                    prepend-inner-icon="mdi-eye"
                    readonly
                    rounded
                    filled
                    hide-details
                    @click:prepend-inner="dialog = true"
                  >
                    <v-icon style="vertical-align: middle">mdi-eye</v-icon>
                    <template v-slot:label>
                      Click
                      <v-icon style="vertical-align: middle" small
                        >mdi-eye</v-icon
                      >&#160;to show opening hours
                    </template>
                  </v-text-field>
                  <v-dialog v-model="dialog" max-width="450" persistent>
                    <v-card>
                      <v-card-title class="headline"
                        >Opening hours</v-card-title
                      >
                      <v-card-text>
                        <v-container>
                          <v-row>
                            <v-col cols="12">
                              <v-data-table
                                :headers="headers"
                                :items="restaurant.info.openingHours"
                                hide-default-footer
                              >
                                <template v-slot:item.day="{ item }">
                                  <v-chip v-if="item.day === 'monday'"
                                    >Monday</v-chip
                                  >
                                  <v-chip v-if="item.day === 'tuesday'"
                                    >Tuesday</v-chip
                                  >
                                  <v-chip v-if="item.day === 'wednesday'"
                                    >Wednesday</v-chip
                                  >
                                  <v-chip v-if="item.day === 'thursday'"
                                    >Thursday</v-chip
                                  >
                                  <v-chip v-if="item.day === 'friday'"
                                    >Friday</v-chip
                                  >
                                  <v-chip v-if="item.day === 'saturday'"
                                    >Saturday</v-chip
                                  >
                                  <v-chip v-if="item.day === 'sunday'"
                                    >Sunday</v-chip
                                  >
                                </template>
                                <template v-slot:item.from="props">
                                  <v-edit-dialog
                                    :return-value.sync="props.item.from"
                                    large
                                    cancel-text="Close"
                                    save-text="Save"
                                  >
                                    <div>{{ props.item.from }}</div>
                                    <template v-slot:input>
                                      <div class="mt-4 title">Update time</div>
                                    </template>
                                    <template v-slot:input>
                                      <v-select
                                        menu-props="auto"
                                        :items="time"
                                        style="margin-top: 16px"
                                        v-model="props.item.from"
                                        label="10:00"
                                        single-line
                                        filled
                                        rounded
                                        hide-details
                                      ></v-select>
                                    </template>
                                  </v-edit-dialog>
                                </template>
                                <template v-slot:item.to="props">
                                  <v-edit-dialog
                                    :return-value.sync="props.item.to"
                                    large
                                    cancel-text="Close"
                                    save-text="Save"
                                  >
                                    <div>{{ props.item.to }}</div>
                                    <template v-slot:input>
                                      <div class="mt-4 title">Update time</div>
                                    </template>
                                    <template v-slot:input>
                                      <v-select
                                        menu-props="auto"
                                        :items="time"
                                        style="margin-top: 16px"
                                        v-model="props.item.to"
                                        label="20:00"
                                        single-line
                                        filled
                                        rounded
                                        hide-details
                                      ></v-select>
                                    </template>
                                  </v-edit-dialog>
                                </template>
                              </v-data-table>
                            </v-col>
                          </v-row>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <!-- <v-btn color="primary" text rounded @click="dialog = false">Close</v-btn>
                        <v-btn color="success" text rounded @click="saveOpeningHours()">Save</v-btn>-->
                        <v-btn
                          color="primary"
                          text
                          rounded
                          @click="dialog = false"
                          >Close</v-btn
                        >
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <!-- <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Address
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveRestaurantInfo()"
                >Save</v-btn>
              </v-card-title>
              <v-row>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.street"
                    label="Street"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.city"
                    label="City"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.zip"
                    label="Zip"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-text-field
                    v-model="restaurant.info.contact.address.state"
                    label="State"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>-->
          <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">
                Default order messages
                <v-btn
                  style="margin-left: 8px"
                  color="success"
                  text
                  rounded
                  @click="saveDefaultMessages()"
                  >Save</v-btn
                >
              </v-card-title>
              <v-row>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="restaurant.defaultMessages.confirmation.subject"
                    label="Confirmation subject"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    v-model="restaurant.defaultMessages.confirmation.message"
                    label="Confirmation message"
                    auto-grow
                    rounded
                    filled
                    hint="Placeholders: DELIVERY_TIME and TOTAL_WITH_DELIVERY"
                    persistent-hint
                  ></v-textarea>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    v-model="restaurant.defaultMessages.rejection.subject"
                    label="Rejection subject"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    v-model="restaurant.defaultMessages.rejection.message"
                    label="Rejection message"
                    auto-grow
                    rounded
                    filled
                    hide-details
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <!-- <v-card-text>
            <v-container>
              <v-card-title class="headline pl-0" style="color: black">Access</v-card-title>
              <v-row></v-row>
            </v-container>
          </v-card-text>-->
        </v-card>
      </v-tab-item>
    </v-tabs>
  </v-skeleton-loader>
</template>

<script>
import settingsService from "../services/settings.service";

export default {
  data: () => ({
    loading: true,
    dialog: false,
    timePickerMenuStatus1: false,
    timePickerMenuStatus2: false,
    time: [
      "0:00",
      "0:30",
      "1:00",
      "1:30",
      "2:00",
      "2:30",
      "3:00",
      "3:30",
      "4:00",
      "4:30",
      "5:00",
      "5:30",
      "6:00",
      "6:30",
      "7:00",
      "7:30",
      "8:00",
      "8:30",
      "9:00",
      "9:30",
      "10:00",
      "10:30",
      "11:00",
      "11:30",
      "12:00",
      "12:30",
      "13:00",
      "13:30",
      "14:00",
      "14:30",
      "15:00",
      "15:30",
      "16:00",
      "16:30",
      "17:00",
      "17:30",
      "18:00",
      "18:30",
      "19:00",
      "19:30",
      "20:00",
      "20:30",
      "21:00",
      "21:30",
      "22:00",
      "22:30",
      "23:00",
      "23:30",
    ],
    headers: [
      { text: "Day", value: "day", sortable: false },
      { text: "From", value: "from", sortable: false },
      { text: "To", value: "to", sortable: false },
    ],
    desserts: [
      {
        day: "monday",
        from: "14:00",
        to: "20:00",
      },
      {
        day: "tuesday",
        from: "14:00",
        to: "20:00",
      },
      {
        day: "wednesday",
        from: "14:00",
        to: "20:00",
      },
      {
        day: "thursday",
        from: "14:00",
        to: "20:00",
      },
      {
        day: "friday",
        from: "14:00",
        to: "20:00",
      },
      {
        day: "saturday",
        from: "14:00",
        to: "20:00",
      },
      {
        day: "sunday",
        from: "14:00",
        to: "20:00",
      },
    ],
    account: {
      info: {
        firstName: null,
        lastName: null,
        mobile: null,
      },
      email: null,
      currentPassword: null,
      newPassword: null,
    },
    restaurant: {
      isOpen: false,
      dailyMenuOrderingTime: {
        from: null,
        to: null,
      },
      businessDetails: {
        legalName: null,
        businessId: null,
        taxId: null,
        vat: null,
        address: {
          street: null,
          city: null,
          zip: null,
          state: null,
        },
      },
      names: {
        name: null,
        urlName: null,
        voiceName: null,
      },
      info: {
        openingHours: [
          {
            day: "monday",
            from: "14:00",
            to: "20:00",
          },
          {
            day: "tuesday",
            from: "14:00",
            to: "20:00",
          },
          {
            day: "wednesday",
            from: "14:00",
            to: "20:00",
          },
          {
            day: "thursday",
            from: "14:00",
            to: "20:00",
          },
          {
            day: "friday",
            from: "14:00",
            to: "20:00",
          },
          {
            day: "saturday",
            from: "14:00",
            to: "20:00",
          },
          {
            day: "sunday",
            from: "14:00",
            to: "20:00",
          },
        ],
        description: null,
        contact: {
          website: null,
          email: null,
          mobile: null,
          address: {
            street: null,
            city: null,
            zip: null,
            state: null,
          },
        },
      },
      defaultMessages: {
        confirmation: {
          subject: null,
          message: null,
        },
        rejection: {
          subject: null,
          message: null,
        },
      },
    },
  }),
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      const user = await settingsService.getUser();
      this.account.info = user.info;
      this.account.email = user.email;
      const restaurant = await settingsService.getRestaurant(
        localStorage.getItem("restaurantId")
      );
      // if (restaurant.businessDetails) this.restaurant.businessDetails = restaurant.businessDetails;
      // if (restaurant.names) this.restaurant.names = restaurant.names;
      // if (restaurant.info) this.restaurant.info = restaurant.info;
      // if (!this.restaurant.info.openingHours || this.restaurant.info.openingHours.length === 0) this.restaurant.info.openingHours = this.desserts
      // if (restaurant.defaultMessages) this.restaurant.defaultMessages = restaurant.defaultMessages;

      this.restaurant = {
        ...this.restaurant,
        ...restaurant,
        info: { ...this.restaurant.info, ...restaurant.info },
      };
      if (
        !this.restaurant.info.openingHours ||
        this.restaurant.info.openingHours.length === 0
      )
        this.restaurant.info.openingHours = this.desserts;
      console.log(this.restaurant);

      this.loading = false;
    },
    async saveUserInfo() {
      this.loading = true;
      const info = this.account.info;
      const resp = await settingsService.editUserInfo(info);
      if (resp) {
        this.account.info = resp;
        this.loading = false;
      }
    },
    async saveOnlineOrdering() {
      this.loading = true;
      const status = this.restaurant.isOpen;
      const times = this.restaurant.dailyMenuOrderingTime;
      const resp = await settingsService.editOnlineOrdering(
        localStorage.getItem("restaurantId"),
        status,
        times
      );
      if (resp) {
        this.restaurant.isOpen = resp.isOpen;
        this.restaurant.dailyMenuOrderingTime = resp.dailyMenuOrderingTime;
        this.$emit("on-open-status-change", this.restaurant.isOpen);
        this.loading = false;
      }
    },
    async saveBusinessDetails() {
      this.loading = true;
      const businessDetails = this.restaurant.businessDetails;
      const resp = await settingsService.editBusinessDetails(
        localStorage.getItem("restaurantId"),
        businessDetails
      );
      if (resp) {
        this.restaurant.businessDetails = resp;
        this.loading = false;
      }
    },
    async saveNames() {
      this.loading = true;
      const names = this.restaurant.names;
      const resp = await settingsService.editNames(
        localStorage.getItem("restaurantId"),
        names
      );
      if (resp) {
        this.restaurant.names = resp;
        this.loading = false;
      }
    },
    // async saveRestaurantInfo() {
    //   this.loading = true;
    //   const info = {
