<template>
  <v-stepper v-model="e6" vertical elevation="0">
    <!-- <v-stepper-header>
      <v-stepper-step :complete="e6 > 1" editable step="1" edit-icon="$complete">Names</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e6 > 2" editable step="2" edit-icon="$complete">Business details</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e6 > 3" editable step="3" edit-icon="$complete">Information</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step editable step="4" edit-icon="$complete">Default order messages</v-stepper-step>
    </v-stepper-header>-->

    <v-stepper-step :complete="e6 > 1" editable step="1" edit-icon="$complete">
      Names
      <small>Names that represent your brand</small>
    </v-stepper-step>
    <v-stepper-content step="1">
      <v-card-text>
        <v-card-title class="headline" style="color: black">
          Names
          <v-btn style="margin-left: 8px" color="primary" text rounded @click="e6 = 2">Continue</v-btn>
        </v-card-title>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="restaurant.names.name" label="Name" rounded filled hide-details></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="restaurant.names.urlName"
              label="Url name"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="restaurant.names.voiceName"
              label="Voice name"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-text>
    </v-stepper-content>

    <v-stepper-step :complete="e6 > 2" editable step="2" edit-icon="$complete">
      Business details
      <small>Business details on invoices</small>
    </v-stepper-step>
    <v-stepper-content step="2">
      <v-card-text>
        <v-card-title class="headline" style="color: black">
          Business details
          <v-btn style="margin-left: 8px" color="primary" text rounded @click="e6 = 3">Continue</v-btn>
        </v-card-title>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.legalName"
              label="Legal name"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.businessId"
              label="Business ID"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.taxId"
              label="Tax ID"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.vat"
              label="VAT reg. no."
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.address.street"
              label="Street"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.address.city"
              label="City"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.address.zip"
              label="Zip"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.businessDetails.address.state"
              label="State"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-text>
    </v-stepper-content>

    <v-stepper-step :complete="e6 > 3" editable step="3" edit-icon="$complete">
      Information
      <small>General information about restaurant</small>
    </v-stepper-step>
    <v-stepper-content step="3">
      <v-card-text>
        <v-card-title class="headline" style="color: black">
          Information
          <v-btn style="margin-left: 8px" color="primary" text rounded @click="e6 = 4">Continue</v-btn>
        </v-card-title>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="restaurant.info.contact.website"
              label="Website"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="restaurant.info.contact.email"
              label="Email"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="restaurant.info.contact.mobile"
              label="Mobile"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.info.contact.address.street"
              label="Street"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.info.contact.address.city"
              label="City"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.info.contact.address.zip"
              label="Zip"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              v-model="restaurant.info.contact.address.state"
              label="State"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-textarea
              v-model="restaurant.info.description"
              label="Description"
              auto-grow
              rounded
              filled
              hide-details
            ></v-textarea>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field
              label="Opening hours"
              prepend-inner-icon="mdi-eye"
              readonly
              rounded
              filled
              hide-details
              @click:prepend-inner="dialog = true"
            >
              <v-icon style="vertical-align: middle">mdi-eye</v-icon>
              <template v-slot:label>
                Click
                <v-icon style="vertical-align: middle" small>mdi-eye</v-icon>&#160;to show opening hours
              </template>
            </v-text-field>
            <v-dialog v-model="dialog" max-width="450" persistent>
              <v-card>
                <v-card-title class="headline">Opening hours</v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-data-table
                          :headers="headers"
                          :items="restaurant.info.openingHours"
                          hide-default-footer
                        >
                          <template v-slot:item.day="{ item }">
                            <v-chip v-if="item.day === 'monday'">Monday</v-chip>
                            <v-chip v-if="item.day === 'tuesday'">Tuesday</v-chip>
                            <v-chip v-if="item.day === 'wednesday'">Wednesday</v-chip>
                            <v-chip v-if="item.day === 'thursday'">Thursday</v-chip>
                            <v-chip v-if="item.day === 'friday'">Friday</v-chip>
                            <v-chip v-if="item.day === 'saturday'">Saturday</v-chip>
                            <v-chip v-if="item.day === 'sunday'">Sunday</v-chip>
                          </template>
                          <template v-slot:item.from="props">
                            <v-edit-dialog
                              :return-value.sync="props.item.from"
                              large
                              cancel-text="Close"
                              save-text="Save"
                            >
                              <div>{{ props.item.from }}</div>
                              <template v-slot:input>
                                <div class="mt-4 title">Update time</div>
                              </template>
                              <template v-slot:input>
                                <v-select
                                  menu-props="auto"
                                  :items="time"
                                  style="margin-top: 16px"
                                  v-model="props.item.from"
                                  label="10:00"
                                  single-line
                                  filled
                                  rounded
                                  hide-details
                                ></v-select>
                              </template>
                            </v-edit-dialog>
                          </template>
                          <template v-slot:item.to="props">
                            <v-edit-dialog
                              :return-value.sync="props.item.to"
                              large
                              cancel-text="Close"
                              save-text="Save"
                            >
                              <div>{{ props.item.to }}</div>
                              <template v-slot:input>
                                <div class="mt-4 title">Update time</div>
                              </template>
                              <template v-slot:input>
                                <v-select
                                  menu-props="auto"
                                  :items="time"
                                  style="margin-top: 16px"
                                  v-model="props.item.to"
                                  label="20:00"
                                  single-line
                                  filled
                                  rounded
                                  hide-details
                                ></v-select>
                              </template>
                            </v-edit-dialog>
                          </template>
                        </v-data-table>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <!-- <v-btn color="primary" text rounded @click="dialog = false">Close</v-btn>
                  <v-btn color="success" text rounded @click="saveOpeningHours()">Save</v-btn>-->
                  <v-btn color="primary" text rounded @click="dialog = false">Close</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-col>
          <v-col cols="12" md="6">
            <v-select
              v-model="restaurant.currency"
              :items="['EUR', 'CZK', 'GBP', 'PLN']"
              label="Currency"
              single-line
              filled
              rounded
              hint="Currency cannot be changed in the future"
              persistent-hint
              required
            ></v-select>
          </v-col>
        </v-row>
      </v-card-text>
    </v-stepper-content>

    <v-stepper-step editable step="4" edit-icon="$complete">
      Default order messages
      <small>These messages will be used as email template which is sent to customer after reviewing the order</small>
    </v-stepper-step>
    <v-stepper-content step="4">
      <v-card-text>
        <v-card-title class="headline" style="color: black">
          Default order messages
          <v-btn
            style="margin-left: 8px"
            color="success"
            text
            rounded
            @click="createRestaurant()"
          >Create</v-btn>
        </v-card-title>
        <v-row>
          <v-col cols="12" md="6">
            <v-text-field
              v-model="restaurant.defaultMessages.confirmation.subject"
              label="Confirmation subject"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-textarea
              v-model="restaurant.defaultMessages.confirmation.message"
              label="Confirmation message"
              auto-grow
              rounded
              filled
              hint="Placeholders: DELIVERY_TIME and TOTAL_WITH_DELIVERY"
              persistent-hint
            ></v-textarea>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field
              v-model="restaurant.defaultMessages.rejection.subject"
              label="Rejection subject"
              rounded
              filled
              hide-details
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-textarea
              v-model="restaurant.defaultMessages.rejection.message"
              label="Rejection message"
              auto-grow
              rounded
              filled
              hide-details
            ></v-textarea>
          </v-col>
        </v-row>
      </v-card-text>
    </v-stepper-content>
  </v-stepper>
</template>

<script>
import restaurantService from "../services/restaurant.service";

export default {
  data: () => ({
    loading: true,
    e6: 1,
    dialog: false,
    time: [
      "0:00",
      "0:30",
      "1:00",
      "1:30",
      "2:00",
      "2:30",
      "3:00",
      "3:30",
      "4:00",
      "4:30",
      "5:00",
      "5:30",
      "6:00",
      "6:30",
      "7:00",
      "7:30",
      "8:00",
      "8:30",
      "9:00",
      "9:30",
      "10:00",
      "10:30",
      "11:00",
      "11:30",
      "12:00",
      "12:30",
      "13:00",
      "13:30",
      "14:00",
      "14:30",
      "15:00",
      "15:30",
      "16:00",
      "16:30",
      "17:00",
      "17:30",
      "18:00",
      "18:30",
      "19:00",
      "19:30",
      "20:00",
      "20:30",
      "21:00",
      "21:30",
      "22:00",
      "22:30",
      "23:00",
      "23:30"
    ],
    headers: [
      { text: "Day", value: "day", sortable: false },
      { text: "From", value: "from", sortable: false },
      { text: "To", value: "to", sortable: false }
    ],
    desserts: [
      {
        day: "monday",
        from: "14:00",
        to: "20:00"
      },
      {
        day: "tuesday",
        from: "14:00",
        to: "20:00"
      },
      {
        day: "wednesday",
        from: "14:00",
        to: "20:00"
      },
      {
        day: "thursday",
        from: "14:00",
        to: "20:00"
      },
      {
        day: "friday",
        from: "14:00",
        to: "20:00"
      },
      {
        day: "saturday",
        from: "14:00",
        to: "20:00"
      },
      {
        day: "sunday",
        from: "14:00",
        to: "20:00"
      }
    ],
    account: {
      info: {
        firstName: null,
        lastName: null,
        mobile: null
      },
      email: null,
      currentPassword: null,
      newPassword: null
    },
    restaurant: {
      businessDetails: {
        legalName: null,
        businessId: null,
        taxId: null,
        vat: null,
        address: {
          street: null,
          city: null,
          zip: null,
          state: null
        }
      },
      names: {
        name: null,
        urlName: null,
        voiceName: null
      },
      info: {
        openingHours: [],
        description: null,
        contact: {
          website: null,
          email: null,
          mobile: null,
          address: {
            street: null,
            city: null,
            zip: null,
            state: null
          }
        }
      },
      defaultMessages: {
        confirmation: {
          subject: null,
          message: null
        },
        rejection: {
          subject: null,
          message: null
        }
      },
      currency: null
    }
  }),
  created() {
    if (
      !this.restaurant.info.openingHours ||
      this.restaurant.info.openingHours.length === 0
    )
      this.restaurant.info.openingHours = this.desserts;
  },
  methods: {
    async createRestaurant() {
      Object.keys(this.restaurant.names).forEach(
        key =>
          this.restaurant.names[key] == null &&
          delete this.restaurant.names[key]
      );
      
      const newRestaurant = await restaurantService.createRestaurant(this.restaurant)

      if (localStorage.access) {
        let access = JSON.parse(localStorage.access);
        access.push(newRestaurant)
        localStorage.access = JSON.stringify(access)
        localStorage.restaurantId = newRestaurant.restaurantId
        this.$router.push("/dashboard");
      }
    }
  }
};
</script>

<style>
.v-small-dialog__actions .v-btn:nth-child(2) {
  color: #13e46d !important;
}

.v-small-dialog__actions .v-btn {
  border-radius: 28px;
}

.v-stepper .v-card__text {
  padding-top: 0px;
}

.v-stepper {
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
}
</style>
