<template>
  <v-skeleton-loader :loading="loading" type="article, list-item-two-line@2, actions">
    <v-container>
      <v-row>
        <v-card-title class="headline" style="color: black">
          Show transactions
          <v-chip-group mandatory active-class="primary--text" style="margin-left: 8px">
            <v-chip @click="generateChart('hourly')">hourly</v-chip>
            <v-chip @click="generateChart('daily')">daily</v-chip>
            <v-chip @click="generateChart('weekly')">weekly</v-chip>
            <v-chip @click="generateChart('monthly')">monthly</v-chip>
          </v-chip-group>
        </v-card-title>
      </v-row>
      <v-row>
        <v-col cols="12" md="6">
          <v-card color="secondary" elevation="24" style="position: relative; min-height: 350px">
            <div style="position: absolute; top: 0px; padding: 16px; color: white">
              <v-icon color="primary" style="margin-bottom: 8px">mdi-currency-usd-circle-outline</v-icon>
              <h2>{{remainingRestaurantCredit.toFixed(2)}} €</h2>
              <span color="white">Remaining restaurant credit</span>
            </div>
            <div style="position: absolute; bottom: 0px; width: 100%">
              <apexchart height="250" type="area" :options="chartOptions" :series="series"></apexchart>
            </div>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-card elevation="24" flat>
            <v-card-title>Transaction history</v-card-title>
            <v-data-table
              :headers="headers"
              :items="transactions"
              sort-by="created"
              :sort-desc="true"
            >
              <template
                v-slot:item.created="{ item }"
              >{{ new Date(item.created).toLocaleDateString(undefined, { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', hour: '2-digit', minute: '2-digit' }) }}</template>
              <template v-slot:item.amount="{ item }">
                <v-chip v-if="Number(item.amount) > 0" color="success">{{ item.amount }} €</v-chip>
                <v-chip v-if="Number(item.amount) < 0" color="error">{{ item.amount }} €</v-chip>
                <v-chip v-if="Number(item.amount) === 0">{{ item.amount }} €</v-chip>
              </template>
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-skeleton-loader>
</template>

<script>
// import ApexCharts from "apexcharts";
import transactionService from "../services/transaction.service";
import chartService from "../services/chart.service";

export default {
  data: () => ({
    loading: true,
    remainingRestaurantCredit: 0,
    transactions: [],
    remainingRestaurantCreditChartData: [],
    chartOptions: {
      chart: {
        type: "area",
        toolbar: {
          show: false
        },
        zoom: {
          enabled: false
        }
      },
      stroke: {
        show: true,
        curve: "smooth",
        lineCap: "round",
        width: 5
      },
      colors: ["#0360ec"], //#ffaf40
      fill: {
        colors: ["#0360ec"],
        type: "gradient",
        gradient: {
          colorStops: [
            {
              offset: 50,
              color: "#100f0f",
              opacity: 0.0
            },
            {
              offset: 100,
              color: "#0360ec",
              opacity: 0.55
            }
          ]
        }
      },
      dataLabels: {
        enabled: false // hodnoty na grafe
      },
      grid: {
        padding: {
          left: 0,
          right: 0
        },
        show: false
      },
      yaxis: {
        show: false
      },
      tooltip: {
        enabled: true,
        x: {
          show: false
        },
        marker: false
      },
      xaxis: {
        type: "datetime",
        labels: {
