    //     type: "gradient",
    //     gradient: {
    //       shadeIntensity: 0,
    //       opacityFrom: 0.55,
    //       opacityTo: 0.0,
    //       stops: [5, 90, 100]
    //     }
    //   },
    //   dataLabels: {
    //     enabled: false // hodnoty na grafe
    //   },
    //   grid: {
    //     padding: {
    //       left: 0,
    //       right: 0
    //     },
    //     show: false
    //   },
    //   yaxis: {
    //     show: false
    //   },
