    //       show: true, // x os- cas
    //       offsetY: 0,
    //       style: {
    //         colors: "#fff"
    //       }
    //     },
    //     axisBorder: {
    //       show: false
    //     },
    //     axisTicks: {
    //       show: false
    //     },
    //     tooltip: {
    //       enabled: false
    //     }
    //   }
    // },
    headers: [
      {
        text: "Date",
        value: "created"
      },
      { text: "Amount", value: "amount" }
    ]
  }),
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  // mounted() {
  //   if (document.querySelector("#creditChart")) {
  //     let chart = new ApexCharts(
  //       document.querySelector("#creditChart"),
  //       this.options
  //     );
  //     this.chart.render();
  //   }
  // },
  methods: {
    async fetchData() {
      const resp = await transactionService.getTransactions(
        localStorage.getItem("restaurantId")
      );
      this.transactions = resp.transactions;
      this.remainingRestaurantCredit = resp.remainingRestaurantCredit;
      this.generateChart("hourly");
      this.loading = false;
    },
    async generateChart(timePeriod) {
      const remainingRestaurantCreditChartData = await chartService.generateChartData(
        this.remainingRestaurantCredit,
        this.transactions,
        "credits",
        timePeriod
      );

      this.series = [
        {
          data: remainingRestaurantCreditChartData
        }
      ];
