    //     this.remainingRestaurantCredit,
    //     this.transactions,
    //     "ordersValue",
    //     timePeriod
    //   );
    //   console.log(JSON.stringify(remainingRestaurantCreditChartData));
    //   ApexCharts.exec("mychart", "updateSeries", [
