export default {
  async generateChartData(remainingCredit, transactions, type, timePeriod) {
    const MS_PER_HOUR = 3600 * 1000;
    const MS_PER_DAY = 3600 * 1000 * 24;
    const MS_PER_WEEK = 3600 * 1000 * 24 * 7;
    const MS_PER_MONTH = 3600 * 1000 * 24 * 30;

    const startTime = new Date(transactions[0].created).setHours(
      new Date(transactions[0].created).getHours(),
      0,
      0
    );
    let groupedByTimePeriod = [];
    let outputDataSeries = [];

    const groupByTimePeriod = ms => {
      const filteredTransactions = transactions.filter(
        transaction => transaction.amount < 0
      );
      filteredTransactions.forEach(input => {
        let time = input.created;
        let hour = Math.floor((time - startTime) / ms);
        if (!groupedByTimePeriod[hour]) groupedByTimePeriod[hour] = [];
        groupedByTimePeriod[hour].push(input);
      });

      for (let i = 0; i < groupedByTimePeriod.length; i++) {
        if (!groupedByTimePeriod[i]) groupedByTimePeriod[i] = [];
      }
    };

    const generateDataSeries = (ms, type) => {
      if (type === "orders") {
        groupedByTimePeriod.forEach(e => {
          if (e.length !== 0) {
            outputDataSeries.push([e[e.length - 1].created, e.length]);
          } else {
            outputDataSeries.push([
              outputDataSeries[outputDataSeries.length - 1][0] + ms,
              0
            ]);
          }
        });
      } else if (type === "revenue") {
        groupedByTimePeriod.forEach(e => {
          if (e.length !== 0) {
            let total = 0;
            if (e.length === 1) total = (e[0].amount * -1 * 100) / (0.05 * 100);
            else
              total = e.reduce(
                (sum, { amount }) => sum + (amount * -1 * 100) / (0.05 * 100),
                0
              );
            outputDataSeries.push([
              e[e.length - 1].created,
              Number(total.toFixed(2))
            ]);
          } else {
            outputDataSeries.push([
              outputDataSeries[outputDataSeries.length - 1][0] + ms,
              0
            ]);
          }
        });
      } else if (type === "ordersValue") {
        groupedByTimePeriod.forEach(e => {
          if (e.length !== 0) {
            let total = 0;
            if (e.length === 1) total = (e[0].amount * -1 * 100) / (0.05 * 100);
            else
              total = e.reduce(
                (sum, { amount }) => sum + (amount * -1 * 100) / (0.05 * 100),
                0
              );
            outputDataSeries.push([
              e[e.length - 1].created,
              Number((total / e.length).toFixed(2))
            ]);
          } else {
            outputDataSeries.push([
              outputDataSeries[outputDataSeries.length - 1][0] + ms,
              0
            ]);
          }
        });
      } else if (type === "credits") {
        // const reverse = (array) => {
        //   let output = [];
        //   while (array.length) {
        //     output.push(array.pop());
        //   }
        //   return output;
        // };
        // const reversedOld = transactions.reverse();
        // const reversed = reverse(transactions)
        // console.warn(JSON.stringify(reversedOld), JSON.stringify(reversed))

        const reverseArr = (input) => {
          var ret = new Array();
          for (var i = input.length - 1; i >= 0; i--) {
            ret.push(input[i]);
          }
          return ret;
        }
        const reversed = reverseArr(transactions);
        
        outputDataSeries.push([Date.now(), remainingCredit]);
        reversed.forEach((e, i) => {
          const total = reversed[i - 1]
            ? reversed[i - 1].amount * -1 +
              outputDataSeries[outputDataSeries.length - 1][1]
            : outputDataSeries[outputDataSeries.length - 1][1];
          outputDataSeries.push([e.created, Number(total.toFixed(2))]);
        });
      }
    };

    if (timePeriod === "hourly") {
      if (type !== "credits") groupByTimePeriod(MS_PER_HOUR);
      generateDataSeries(MS_PER_HOUR, type);
      return outputDataSeries.filter(
        e => e[0] <= Date.now() && e[0] > Date.now() - MS_PER_HOUR * 12
      );
    } else if (timePeriod === "daily") {
      if (type !== "credits") groupByTimePeriod(MS_PER_DAY);
      generateDataSeries(MS_PER_DAY, type);
      return outputDataSeries.filter(
        e => e[0] <= Date.now() && e[0] > Date.now() - MS_PER_DAY * 7
      );
    } else if (timePeriod === "weekly") {
      if (type !== "credits") groupByTimePeriod(MS_PER_WEEK);
      generateDataSeries(MS_PER_WEEK, type);
      return outputDataSeries.filter(
        e => e[0] <= Date.now() && e[0] > Date.now() - MS_PER_WEEK * 4
      );
    } else if (timePeriod === "monthly") {
      if (type !== "credits") groupByTimePeriod(MS_PER_MONTH);
      generateDataSeries(MS_PER_MONTH, type);
      return outputDataSeries.filter(
        e => e[0] <= Date.now() && e[0] > Date.now() - MS_PER_MONTH * 12
      );
    }
  }
};
